import profileImg from '../assets/profilePicture.jpeg';
import React from 'react';
import NavBar from './Navbar';
import Footer from './Footer';
export default class Stats extends React.Component {

    render() {

        const imdb = (function (d, s, id) {
            var js, stags = d.getElementsByTagName(s)[0];
            if (d.getElementById(id)) {
                return;
            }
            js = d.createElement(s);
            js.id = id;
            js.src = "https://ia.media-imdb.com/images/G/01/imdb/plugins/rating/js/rating.js";
            stags.parentNode.insertBefore(js, stags);
        }
        )(document, "script", "imdb-rating-api");
        return (






            <>
                <header className="App-header">

                    <NavBar />

                </header>
                <main class="main">
                    <section id="about">
                        
                        <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.1)' }} className="d-flex flex-column  flex-sm-row  p-2">
                            
                            <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.1)' }} className="d-flex flex-column p-2">
                                <div className="p-2"><a href='https://www.imdb.com/user/ur84057902/ratings' > My ratings</a> </div>
                                <div className="p-2"><a href='https://www.imdb.com/user/ur84057902/watchlist' > My watchlist</a> </div>

                                
                            </div>

                        </div>

                    </section>



                </main>
                <Footer />
            </>
        );

        
    }
}